import * as GeoJsonDataSource from '../../datasource/geojson'

function plugin (Vue, options = {}) {
  if (plugin.installed) {
    return
  }
  plugin.installed = true
  Vue.use(GeoJsonDataSource)
  Vue.component(VcKrigingMap.name, VcKrigingMap)
}

export default plugin

export {
  VcKrigingMap,
  plugin as install
}
